import { useEffect, useState } from "react";
import {
  Container,
  Row,
  Col,
  Card,
  Form,
  Dropdown,
  Button,
} from "react-bootstrap";
import { BiSearch } from "react-icons/bi";
import { BsChevronDown } from "react-icons/bs";
import IMAGES from "../../../assets/images";
import {
  getAllMessage,
  getInboxList,
  messageIsRead,
  pushSendMessade,
  sendMessage,
} from "../../../app/features/chat/chat.slice";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";
import { convertTimeIntoUTC } from "../../../components/GenericActions";
import { useRef } from "react";
import { socket } from "../../../authorization/ProtectedRoute";

const Chat = () => {
  const dispatch = useDispatch();
  const bottomRef = useRef(null);
  const inputRef = useRef(null);
  const [newMessage, setNewMessage] = useState("");
  const [selectedChat, setSelectedChat] = useState();
  const { user } = useSelector((state) => state.auth);
  const { allInboxList, allMessages } = useSelector((state) => state?.chat);

  const userId = user?.userId;
  const handleChatClick = (chat) => {
    setSelectedChat(chat);
    setNewMessage("");
    const finalData = {
      inboxId: selectedChat?.inboxId,
    };
    dispatch(messageIsRead(finalData));
  };

  const handleNewMessageChange = (event) => {
    setNewMessage(event.target.value);
  };
  useEffect(() => {
    if (socket) {
      socket?.on("connect", () => {
        console.log("signal1", socket?.connected);
      });
      socket?.emit("connectToUserRoom", { userId: userId }, () => {
        console.log("signal2", socket?.connected);
        // setAgainCheckStatus(true);
      });
      if (socket?.connected === false) {
        socket?.on("reconnect", () => {
          console.log("reconnect", socket?.connected);
        });
      }
      if (socket?.connected === true) {
        console.log("refttytfconnect", socket?.connected);
      }
    }
  }, [userId, socket]);

  useEffect(() => {
    if (socket?.connected === true) {
      socket?.on("messageData", (data) => {
        if (data?.data) {
          const jsonData = data?.data?.data;
          const senderMessage = JSON.parse(jsonData);
          const finalData = {
            createdDate: convertTimeIntoUTC(new Date()),
            file: senderMessage?.file ? senderMessage?.file : null,
            fileUrl: senderMessage?.fileUrl ? senderMessage?.fileUrl : null,
            fromUserId: senderMessage?.fromUserId,
            fromUserName: senderMessage?.fromUserName
              ? senderMessage?.fromUserName
              : selectedChat?.userName
              ? selectedChat?.userName
              : null,
            image: senderMessage?.image ? senderMessage?.image : null,
            imageUrl: senderMessage?.imageUrl ? senderMessage?.imageUrl : null,
            inboxId: senderMessage?.inboxId,
            message: senderMessage?.message,
            messageId: senderMessage?.messageId
              ? senderMessage?.messageId
              : null,
            toUserId: senderMessage?.toUserId,
            toUserName: senderMessage?.toUserName
              ? senderMessage?.toUserName
              : null,
          };
          dispatch(pushSendMessade(finalData));
        }
      });
    }
  }, [socket, selectedChat]);

  const moveToNext = (data) => {
    socket.emit("sendMessage", {
      userIds: [
        {
          id: selectedChat?.userId,
        },
      ],
      data: JSON.stringify(data?.data),
      type: "inteli_health",
      // uuid: v4(),
    });
  };
  const handleNewMessageSubmit = (event) => {
    const finalData = {
      inboxId: selectedChat?.inboxId,
      fromUserId: user?.userId,
      toUserId: selectedChat?.userId,
      message: newMessage ? newMessage : "",
      isChat: true,
    };
    dispatch(sendMessage({ finalData, moveToNext }));
    event.preventDefault();
    setNewMessage("");
  };

  useEffect(() => {
    const finalData = {
      inboxId: selectedChat?.inboxId,
    };
    dispatch(getAllMessage(finalData));
  }, [dispatch, selectedChat?.inboxId]);

  useEffect(() => {
    const finalData = {
      search: "",
    };
    dispatch(getInboxList(finalData));
  }, [dispatch]);

  useEffect(() => {
    // ðŸ‘‡ï¸ scroll to bottom every time messages change
    bottomRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [allMessages]);

  const handleKeyDown = (e) => {
    if (e.which === 32 && inputRef.current.selectionStart === 0) {
      e.preventDefault();
    }
  };

  return (
    <>
      <Card className="h-100 chatSection">
        <Card.Body className="py-0 px-0">
          <Container fluid className="">
            <Row className="staff-chat">
              <Col
                xl={4}
                lg={12}
                md={12}
                className="borderRight pt-4 paddingBottom chat-box-shadow-lt"
              >
                <div>
                  <h3 className="fw-bold mb-3 px-4">Messages</h3>
                  <span className="d-flex align-self-center justify-content-center px-4">
                    <Form.Control
                      type="text"
                      placeholder="Search"
                      className="w-100"
                      aria-label="Search"
                    />
                    <BiSearch size={22} className="searchbar-icon" />
                  </span>
                </div>
                <div className="mt-2">
                  <div className="px-4">
                    <Dropdown className="user-dropdown chat_dropdown">
                      <Dropdown.Toggle id="dropdown-basic" className="px-0">
                        <span className="">
                          Sort By <BsChevronDown className="" />
                        </span>
                      </Dropdown.Toggle>
                      <Dropdown.Menu className="">
                        <Dropdown.Item className="">Chat</Dropdown.Item>
                        <Dropdown.Item>Name</Dropdown.Item>
                      </Dropdown.Menu>
                    </Dropdown>
                  </div>
                  <div className="staff-inbox">
                    {allInboxList?.map((item) => {
                      return (
                        <div
                          key={item.inboxId}
                          className={`userChat mobileFlex_reverse d-flex justify-content-between align-items-center chatList_margin ${
                            selectedChat &&
                            selectedChat.inboxId === item.inboxId
                              ? "activeChat"
                              : "hoverChat"
                          }`}
                          onClick={() => handleChatClick(item)}
                        >
                          <div className="d-flex mobileFlex_between align-items-center px-4">
                            <span>
                              <img
                                className="w-25 rounded-circle"
                                src={
                                  item.imageUrl
                                    ? item.imageUrl
                                    : `https://ui-avatars.com/api/?name=${`${item.userName}`}&background=6045eb&color=fff`
                                }
                                alt="UserImage"
                              />
                            </span>
                            <div className="d-flex flex-column align-items-start ms-3 mt-2">
                              <h5 className="mb-1 fw-semibold mobileTextHeading ">
                                {item.userName ? item.userName : "N/A"}
                              </h5>
                              <p className="color_light mobileText  message-container">
                                {item.message ? item.message : ""}
                              </p>
                            </div>
                          </div>
                          <div
                            className="d-flex flex-column justify-content-center align-items-end pr-4"
                            style={{ paddingRight: "12px" }}
                          >
                            <p className="color_light fs-6 mb-1">
                              {item.createdDate
                                ? moment(item.createdDate).format("HH:MM A")
                                : ""}
                            </p>
                            <span className="chatCount text-white rounded">
                              {item.unreadCount ? item.unreadCount : ""}
                            </span>
                          </div>
                        </div>
                      );
                    })}
                  </div>
                </div>
              </Col>
              <Col
                xl={8}
                lg={12}
                md={12}
                className="px-0 chatContent chat-box-shadow-rt"
              >
                {selectedChat ? (
                  <>
                    <div className="d-flex justify-content-between mobileFlex_Col py-2 border-bottom px-4 ">
                      <div className="d-flex justify-content-center align-items-center chatActive mb-sm-0 mb-3">
                        <div className="d-flex align-items-center py-0">
                          <span>
                            <img
                              className="rounded-circle"
                              src={
                                selectedChat.imageUrl
                                  ? selectedChat.imageUrl
                                  : `https://ui-avatars.com/api/?name=${`${selectedChat.userName}`}&background=6045eb&color=fff`
                              }
                              alt="UserImage"
                            />
                          </span>
                          <h5 className="fw-semibold mb-0 ms-3 mobileTextHeading">
                            {selectedChat.userName}
                          </h5>
                        </div>
                      </div>
                      {/* <div className="d-flex justify-content-end align-items-center">
                    <span>
                      <img
                        className="me-4"
                        src={IMAGES.CHAT_CALL_ICON}
                        alt="UserImage"
                      />
                    </span>
                    <span className="">
                      <img
                        className=""
                        src={IMAGES.CHAT_VIDEO_ICON}
                        alt="UserImage"
                      />
                    </span>
                  </div> */}
                    </div>
                    <div>
                      <div
                        className="div-fixed-height"
                        style={{ background: "#FBFBFB" }}
                      >
                        {allMessages !== null
                          ? allMessages?.map((chatData, index) => {
                              return (
                                <>
                                  <div className="d-flex flex-column h-100 px-4 py-3 chatBox">
                                    <div>
                                      {allMessages?.[
                                        index - 1
                                      ]?.createdDate?.split("T")?.[0] !==
                                        chatData?.createdDate?.split(
                                          "T"
                                        )?.[0] && (
                                        <div className="text-center rounded-pill mx-auto chatSpan bg-white">
                                          {moment(chatData?.createdDate).format(
                                            "DD-MM-YYYY"
                                          ) ===
                                          moment(new Date()).format(
                                            "DD-MM-YYYY"
                                          )
                                            ? "Today"
                                            : moment(
                                                chatData?.createdDate
                                              ).format("DD-MM-YYYY")}
                                        </div>
                                      )}

                                      <div>
                                        <Row>
                                          {chatData?.fromUserId !==
                                          user?.userId ? (
                                            <Col
                                              lg={8}
                                              md={6}
                                              className="d-flex flex-column justify-content-start"
                                            >
                                              <div className="chatMessage_left d-flex justify-content-start align-items-center mobileFlex_Col mb-1">
                                                <img
                                                  className="me-2 mb-2 mb-sm-0 rounded-circle"
                                                  style={{ width: "50px", height: "50px" }}
                                                  src={
                                                    chatData?.imageUrl
                                                      ? chatData?.imageUrl
                                                      : `https://ui-avatars.com/api/?name=${chatData?.fromUserName}&background=6045eb&color=fff`
                                                  }
                                                  alt="UserImage"
                                                />
                                                <span className="messageSpan">
                                                  {chatData?.message}
                                                </span>
                                              </div>
                                              <span className="ms-5 chatTime">
                                                {moment(
                                                  chatData?.createdDate
                                                ).format("HH:MM A")}
                                              </span>
                                            </Col>
                                          ) : (
                                            <Col
                                              lg={4}
                                              md={6}
                                              className="chatMessage_right text-white d-flex flex-column align-items-end justify-content-end w-100"
                                            >
                                              <span className="mb-1 mt-3 mobile_margin sender-message">
                                                {chatData?.message}
                                              </span>
                                              <div className="chatTime align-self-end">
                                                {moment(
                                                  chatData?.createdDate
                                                ).format("HH:MM A")}
                                              </div>
                                            </Col>
                                          )}
                                        </Row>
                                      </div>
                                    </div>
                                  </div>
                                  <div ref={bottomRef} />
                                </>
                              );
                            })
                          : "No Message"}
                      </div>
                      <div className="border-top">
                        <div className="d-flex mobileFlex_Col justify-content-between align-items-center px-sm-4">
                          <div className="d-flex justify-content-center">
                            {/* <Button className="bg-transparent">
                              <img
                                className=""
                                src={IMAGES.SMILEYS_ICON}
                                alt="smileys"
                                onClick={handleNewMessageSubmit}
                              />
                            </Button> */}

                            <Button className="bg-transparent" type="file">
                              <img
                                className=""
                                src={IMAGES.CLIP_ICON}
                                alt="clip"
                                onClick={handleNewMessageSubmit}
                              />
                            </Button>
                          </div>

                          <div className="w-100 d-flex miniFlex_Col">
                            <Form.Group className="position-relative py-3 w-100">
                              <Form.Control
                                className="padding rounded-pill"
                                placeholder="Type a message hereâ€¦"
                                value={newMessage}
                                onChange={handleNewMessageChange}
                                ref={inputRef}
                                onKeyDown={handleKeyDown}
                              />
                              <Button
                                className="sendIcon rounded-circle"
                                onClick={handleNewMessageSubmit}
                                disabled={newMessage === "" ? true : false}
                              >
                                <img src={IMAGES.SEND_ICON} alt="messageIcon" />
                              </Button>
                            </Form.Group>

                            {/* <Button className="bg-transparent">
                              <img
                                className=""
                                type="button"
                                src={IMAGES.MIC_ICON}
                                alt="mic"
                                onClick={handleNewMessageSubmit}
                              />
                            </Button> */}
                          </div>
                        </div>
                      </div>
                    </div>
                  </>
                ) : (
                  <h5 className="mb-0 text-center text-black-50 d-flex justify-content-center align-items-center h-100">
                    Welcome to our chat system <br /> Please feel free to start
                    typing your message
                  </h5>
                )}
              </Col>
            </Row>
          </Container>
        </Card.Body>
      </Card>
    </>
  );
};

export default Chat;
